.navbar {
  width: 100%;
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem;
  position: fixed;
  transition: ease-in 0.5s;
  z-index: 99;
}

.navbar.bg-black {
  background-color: black;
}

.title {
  color: red;
  cursor: pointer;
}
